.main-page {
    position: relative;
    width: 100%;
    height: 70%;
}

.main-page > .main-page-box {
    text-align: center;
    padding: 1em 0;
}

.main-page > .main-page-box > select, option {
    cursor: pointer;
}

.main-page > .main-page-box-left {
    position: relative;
    width: 50%;
    float: left;
    text-align: center;
}

.main-page > .main-page-box-right {
    position: relative;
    width: 50%;
    float: left;
    text-align: center;
}

.main-page > .main-page-box-right > form {
    width: 100%;
    height: auto;
    position: relative;
}

.main-page >.main-page-box-left > form {
    width: 100%;
    height: auto;
    position: relative;
}

.token-content {
    margin: 1em 0;
    color: coral;
    white-space: pre-wrap;
    width: 90%;
    border: 1px solid grey;
    padding: 0.5em;
    border: 1px solid darkgray;
    border-radius: 0.3em;
}

.header-content {
    color: darkorchid;
    white-space: pre-wrap;
    width: 90%;
    border: 1px solid grey;
    padding: 0.5em;
    margin: 0.5em 0;
}

.payload-content {
    color: cadetblue;
    white-space: pre-wrap;
    width: 90%;
    border: 1px solid grey;
    padding: 0.5em;
    margin: 0.5em 0;
}

.signature-content {
    color: peru;
    white-space: pre-wrap;
    width: 90%;
    border: 1px solid grey;
    padding: 0.5em;
    margin: 0.5em 0;
}

.encoded-label {
    font-weight: bolder;
}

.decoded-label {
    font-weight: bolder;
}

.token-label {
    font-weight: bolder;
    color: coral;
}

.header-label {
    font-weight: bolder;
    color: darkorchid;
}

.payload-label {
    font-weight: bolder;
    color: cadetblue;
}

.signature-label {
    font-weight: bolder;
    color: peru;
}

.token-submit {
    border: none;
    background-color: whitesmoke;
    text-align: center;
    padding: 1em 1.5em;
    border-radius: 0.5em;
    cursor: pointer;
    font-weight: bolder;
    font-size: 1em;
}

.token-submit:hover {
    opacity: 0.8;
}

.algorithm-options {
    padding: 0.5em;
}

textarea {
    resize: none;
}